import {attr} from "../general/debugFunctions";

/*
      .
,       
  .
*/
function PasswordInput({ extendedLabel = false }) {
    // Debug {
      var required = attr({ debugValue: false, prodValue: true });      
      var pattern = attr({ debugValue: "*", prodValue: "(?=.*\d)(?=.*[a-z-])(?=.*[A-Z-])[A-Za-z--\d]{7,}" });
    //} Debug
  return (
    <div>
      <label htmlFor="password" className="form-label">
        {" "}
        {extendedLabel
          ? " (  7 , : 1 , 1 , 1  )"
          : undefined}
      </label>
      <input
        type="password"
        className="form-control"
        id="password"
        aria-describedby="passwordError"
        name="password"
        pattern= {pattern}
        required={required}
      />
      <div id="passwordError" className="invalid-feedback">
          .
      </div>
    </div>
  );
}

export default PasswordInput;
