import { Route, Routes } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import Account from "./components/pages/Account/Index";
import Card from "./components/pages/Card/Index";
import Home from "./components/pages/Home";
import Login from "./components/pages/Login/Index";
import NewPost from "./components/pages/NewPost";
import Register from "./components/pages/Register";
import Search from "./components/pages/Search";
import RequireAuth from "./components/RequireAuth";
import { useLocation } from "react-router-dom";
import EditPost from "./components/pages/EditPost/Index";

function App() {
  const location = useLocation();
  return (
    
    <div className="container py-3">
      <Routes>
        <Route path={"/login"} element={<Login />} />
        <Route path={"/"} element={<Home />} />
        <Route path={"/search"} element={<Search />} />
        <Route path="/pets/:petId" element={<Card />} />        
        <Route path={"/register"} element={<Register />} />        
        <Route path={"/post/new"} element={<NewPost />} />
        
        {/*     */}
        
        {/*  -   Key. 
        ..    ACCOUNT.    
           .    navigate(ACCOUNT), 
        React    .  
             ,    . 
        
           key. : location.key 
         ,   navigate (  ,   
           )
        
        ..       navigate.               
        */}
        <Route path={"/account"} element={<RequireAuth><Account key={location.key}/></RequireAuth>} />        
        <Route path={"/post/edit/:postId"} element={<EditPost />} />
        
      </Routes>
      <ToastContainer />
    </div>
  );
}

export default App;
