import { attr } from "../general/debugFunctions";

function KindInput({
                    aClassName = null,
                    required = false, //      .      .
                    errorMessage = "  .",
                    defaultValue = null,
                  }) {
  // Debug {
    required = attr({ debugValue: false, prodValue: required });
    var pattern = attr({ debugValue: "*", prodValue: "[--s-]+" });
  // } Debug

  return (
    <div className={aClassName ?? undefined}>
      <label htmlFor="kind" className="form-label">
          (, , )
      </label>
      <input
        type="text"
        className="form-control"
        id="kind"
        aria-describedby="kindError"
        name="kind"
        pattern={pattern}
        required={required}
        defaultValue={defaultValue ? defaultValue : undefined}
      />
      <div id="kindError" className="invalid-feedback">
        {errorMessage}
      </div>
    </div>
  );
}

export default KindInput;
